body {
  background-color: rgb(39, 39, 39);
  color: white;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: rgb(255, 0, 0);
  border-radius: 10px;
}

nav {
  padding: 10px 20px;
  font-size: 1.5rem;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 5px 50px rgba(255, 255, 255, 0.5);
}
.icon {
  width: 60px;
  height: 60px;
}
.NavTile {
  color: rgb(108, 226, 255);
  padding-left: 10px;
}
.NavLogo {
  display: flex;
  align-items: center;
}
.Forecast {
  display: flex;
  justify-content: center;
  padding: 20px 10px 0px 10px;
}

.PrimaryHeader {
  padding: 30px 0px 0px 0px;
  font-size: 1.5rem;

  display: flex;
  overflow-x: auto;
  justify-self: center;
}

.SecondaryHeader {
  margin: 10px;
  padding: 10px;
  font-size: 1.5rem;
  border: 1px solid silver;
  border-radius: 10px;
  display: inline-block;
  white-space: nowrap;
}
.topWindow {
  display: inline-flex;
}
.bottomWindow {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Hour {
  font-size: 2.5rem;
  font-weight: bold;
}
.Data {
  font-size: 0.9rem;
}

footer {
  margin-top: 50px;
  margin-bottom: 10px;
}
small {
  margin-left: 50px;
}
